.popup-wrap {
	display: none;
	overflow: auto hidden;
	z-index: 999;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.4);
	backdrop-filter: blur(3px);
}

.popup-wrap .popup-frame {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
}

.popup-wrap .inner {
	position: relative;
	overflow: hidden auto;
	padding: 50px 70px;
	max-width: 1200px;
