@charset "UTF-8";

@import "base";
@import "login";

/* notice-wrap */
.notice-wrap{padding: 15px 0; height: 90px; border-bottom: 1px solid #D8D8D8;}
.notice-wrap .notice-swiper{overflow: hidden; height: 60px;}
.notice-wrap .items{display: flex; align-items: center; justify-content: center;}
.notice-wrap .items .type{font-size: 20px; font-weight: 700; color: #0058FF;}
.notice-wrap .items h3{margin: 0 20% 0 2%;}
.notice-wrap .items h3 a{font-size: 20px; color: #3B3B3B; text-decoration: underline; text-underline-position : from-font;}
.notice-wrap .items .date{font-size: 20px; color: #3B3B3B;}

/* history-wrap */
.history-wrap{display: flex; align-items: center; padding: 45px 0; border-bottom: 1px solid #D8D8D8;}
.history-wrap .profile-logo{flex-shrink: 0; width: 60px; height: 60px; border-radius: 50%; border: 1px solid #707070;}
.history-wrap .profile-logo img{object-fit: cover;}
.history-wrap .name{margin: 0 16px; font-size: 28px; font-weight: 700;}
.history-wrap .history{font-size: 15px; color: #5A607F;}

.history-wrap.type2{position: relative; padding: 0 40px 40px;}
.history-wrap.type2 .btn-back{position: absolute; top: 20px; left: 0; width: 22px; height: 22px; @include bg('/common/ico_arrow_back.svg');}
.history-wrap.type2 .info-con{@include flex(); margin-left: 20px;}
.history-wrap.type2 .btn-con{margin-left: auto; flex-shrink: 0;}
.history-wrap.type2 .btn-mail{margin: 0 60px;}
.history-wrap.type2 .btn-setting{@include flex(); font-size: 20px; color: #AAA;}
.history-wrap.type2 .btn-setting::before{content: ''; display: inline-block;margin-right: 6px; width: 28px; height: 28px; @include bg('/common/ico_setting.svg');}

/* update-wrap */
.update-wrap{display: flex; align-items: center; padding: 60px 0 30px; border-bottom: 1px solid #D8D8D8;}
.update-wrap .title{margin-right: 27px; font-size: 22px; font-weight: 700;}
.update-wrap .update-list{display: flex; align-items: center;}
.update-wrap .update-list .count{position: relative; padding: 2px 8px; font-size: 20px; font-weight: 700; color: #fff; text-decoration: underline; text-underline-position : from-font; background-color: #F0142F; border-radius: 14px;}
.update-wrap .update-list .total{font-size: 20px; font-weight: 700;}
.update-wrap .update-list .total::before{content:"/"; margin: 0 2px;}

/* course-wrap */
.course-wrap{margin-top: 30px;}
.course-wrap .course-list{display: flex; flex-wrap: wrap; align-items: stretch; justify-content: space-between; margin-bottom: -22px;}
.course-wrap .course-list .items{margin:0 21px 22px 0; padding: 0 32px; width: calc(33.33% - 14px); border: 1px solid #D8D8D8; border-radius: 6px;}
.course-wrap .course-list .items:nth-of-type(3n){margin-right: 0;}
.course-wrap .course-list .items button{width: 100%;}
.course-wrap .course-list .items .type-con{display: flex; align-items: center; justify-content: space-between; padding: 15px 0; border-bottom: 1px solid #D8D8D8;}
.course-wrap .course-list .items .type-con .type{padding: 4px 18px; font-size: 13px; font-weight: 700; color: #fff; background-color: #7C7C7C; border-radius: 18px;}
.course-wrap .course-list .items .type-con .hole-con{display: flex; align-items: center;}
.course-wrap .course-list .items .type-con .hole-con::after{content:""; display: block; margin-left: 25px; width: 8px; height: 8px; border-radius: 50%;}
.course-wrap .course-list .items .type-con .hole-con.red::after{background-color: #F0142F;}
.course-wrap .course-list .items .type-con .hole-con.green::after{background-color: #00FF22;}
.course-wrap .course-list .items .type-con .hole-con .num{margin-right: 12px; padding: 1px 6px; font-size: 20px; font-weight: 700; color: #fff; background-color: #BFBFBF; border-radius: 12px;}
.course-wrap .course-list .items .type-con .hole-con .compose{font-size: 20px; font-weight: 700;}
.course-wrap .course-list .items .detail-con{display: flex; justify-content: center; padding: 15px 0 25px;}
.course-wrap .course-list .items .detail-con .course-item{ margin: 0 auto; flex-shrink: 0; width: 198px;}

.course-wrap .course-list .items .detail-con .size{position: absolute; left: 6px; top: 6px; width: 25px; height: 25px; text-align: center; line-height: 25px; font-size: 14px; font-weight: 700; background-color: #D2D2D2; border-radius: 50%;}
.course-wrap .course-list .items .detail-con .history-con{margin-left: 6%; padding-top: 20px; width: 47%;}
.course-wrap .course-list .items .detail-con .history-con .modify{text-align: center;}
.course-wrap .course-list .items .detail-con .history-con .modify p{font-size: 20px; color: #5A607F;}
.course-wrap .course-list .items .detail-con .history-con .battery{display: flex; margin-top: 28px;}
.course-wrap .course-list .items .detail-con .history-con .battery dt{margin-right: 4px; font-size: 11px;}
.course-wrap .course-list .items .detail-con .history-con .battery dd{display: flex; align-items: center; flex:1;}
.course-wrap .course-list .items .detail-con .history-con .battery dd span{display: block; margin-right: 2px; width: 20%; height: 8px; border-radius: 5px; background-color: #BFBFBF;}
.course-wrap .course-list .items .detail-con .history-con .battery dd span:last-of-type{margin-right: 0;}
.course-wrap .course-list .items .detail-con .history-con .battery .battery1 span:nth-of-type(1){background-color: #f0142f;}
.course-wrap .course-list .items .detail-con .history-con .battery .battery2 span:nth-of-type(1),
.course-wrap .course-list .items .detail-con .history-con .battery .battery2 span:nth-of-type(2),
.course-wrap .course-list .items .detail-con .history-con .battery .battery3 span:nth-of-type(1),
.course-wrap .course-list .items .detail-con .history-con .battery .battery3 span:nth-of-type(2),
.course-wrap .course-list .items .detail-con .history-con .battery .battery3 span:nth-of-type(3),
.course-wrap .course-list .items .detail-con .history-con .battery .battery4 span{background-color: #00ff22;}
.course-wrap .course-list .items .detail-con .history-con .btn{margin-top: 22px; padding: 0; width: 100%; min-width: auto;}

.course-wrap .course-list .items.updated{border-color: #F0142F;}
.course-wrap .course-list .items.updated .type-con .hole-con .num{background-color: #F0142F;}
.course-wrap .course-list .items.updated .detail-con .history-con .btn{background-color: #F0142F;}

.course-wrap .course-list.type2 .items .type-con .hole-con::after{display: none;}
.course-wrap .course-list.type2 .items .type-con .hole-con .date{margin-left: 16px; font-size: 16px; color: #5A607F;}
.course-wrap .course-list.type2 .items .detail-con{justify-content: center; padding: 24px 0 12px;}
.course-wrap .course-list.type2 .items .detail-con .course-item + .course-item{margin-left: 20px;}
.course-wrap .course-list.type2 .items .detail-con .course-item .course-img .img-con{width: 130px; height: 130px;}
.course-wrap .course-list.type2 .items .detail-con .course-item .percent{position: absolute; display: grid; grid-template-columns: repeat(4, 1fr); width: 100%; height: 100%; }
.course-wrap .course-list.type2 .items .detail-con .course-item .percent li{position: relative;@include flex('center'); width: 100%; height: 100%; font-size: 14px; font-weight: 700; color: #fff; z-index: 0;}
.course-wrap .course-list.type2 .items .detail-con .course-item .percent li span{position: relative;}
.course-wrap .course-list.type2 .items .detail-con .course-item .percent li::before{content: ''; position: absolute; width: 100%; height: 100%; background: rgba(0, 88, 255, 0.5);}
.course-wrap .course-list.type2 .items .detail-con .course-item .percent li::after{content: ''; position: absolute; width: calc(100% - 1px); height: calc(100% - 1px); border-bottom: 1px dashed #D8D8D8; border-right: 1px dashed #D8D8D8; z-index: -1;}
.course-wrap .course-list.type2 .items .detail-con .course-item .percent li.c-red::before{background: transparent;}
.course-wrap .course-list.type2 .items .detail-con .course-item .course-info{margin-top: 6px; font-size: 12px; font-weight: 700; text-align: center;}
.course-wrap .course-list.type2 .items .detail-con .course-item .course-info .size{position: static; display: inline-block; margin-right: 7px; font-size: 12px; border: 1px solid #707070; background: transparent;}

.course-wrap .inp-select:nth-child(1){width: 169px;}
.course-wrap .inp-select:nth-child(2){width: 129px;}

.course-wrap .paging{margin-top: 30px;}
.course-wrap .paging .page-list button.on{background-color: $c-gray;}

.course-item + .course-item{margin-left: 20px;}
.course-item .course-img{overflow: hidden; position: relative; @include flex('center'); min-height: 168px; @include bg('content/course_bg.svg', $size: cover); border-radius: 8px;}
.course-item .course-img .img-con{width: 160px; height: 160px;}
.course-item .course-img .img-con canvas{width: 100%; height: 100%;}
.course-item.type2 .course-img .img-con{width: 130px; height: 130px;}
.course-item.type2 .percent{position: absolute; display: grid; grid-template-columns: repeat(4, 1fr); width: 100%; height: 100%; }
.course-item.type2 .percent li{position: relative;@include flex('center'); width: 100%; height: 100%; font-size: 26px; font-weight: 700; color: #fff; z-index: 0;}
.course-item.type2 .percent li span{position: relative;}
.course-item.type2 .percent li::before{content: ''; position: absolute; width: 100%; height: 100%; background: rgba(0, 88, 255, 0.5);}
.course-item.type2 .percent li::after{content: ''; position: absolute; width: calc(100% - 1px); height: calc(100% - 1px); border-bottom: 1px dashed #D8D8D8; border-right: 1px dashed #D8D8D8; z-index: -1;}
.course-item.type2 .percent li.c-red::before{background: transparent;}
.course-item.type2 .course-info{margin-top: 6px; font-size: 12px; font-weight: 700; text-align: center;}
.course-item.type2 .course-info .size{position: static; display: inline-block; margin-right: 7px; font-size: 12px; border: 1px solid #707070; background: transparent;}


.tit-bar{position: relative; padding-left: 24px;}
.tit-bar::before{content:""; position: absolute; left: 0; top: 50%; transform: translateY(-50%); width: 8px; height: 34px; background-color: #bc011a;}

.table-wrap{overflow: auto hidden; margin-bottom: 30px; width: 100%;}
.table-wrap .table-head{overflow: hidden; min-width: 1200px; width: 100%; border-radius: 5px 5px 0 0;}
.table-wrap .table-head .item p{font-size: 20px; font-weight: 700; text-align: center; color: #fff; background-color: #0058ff;}
.table-wrap .table-body{min-width: 1200px; width: 100%; border: 1px solid #d8d8d8;}
.table-wrap .table-body .item{background-color: #fff;}
.table-wrap .table-body .item:nth-of-type(2n){background-color: #efefef;}
.table-wrap .table-body .item p{font-size: 18px; }
.table-wrap .item{display: flex; align-items: center; width: 100%; height: 50px;}
.table-wrap .item p{display: flex; align-items: center; justify-content: center; text-align: center; height: 100%; border-right: 1px solid #d8d8d8;}
.table-wrap .item p:last-of-type{border-right: none;}
.table-wrap .item input[type='checkbox']{display: none;}
.table-wrap .item input[type='checkbox'] + label{display: block; width: 16px; height: 16px; border: 1px solid #D8D8D8; background-color: #fff; border-radius: 3px;}
.table-wrap .item input[type='checkbox']:checked + label{background: #0058FF url("/assets/img/common/ico_checkbox.png") no-repeat center center; background-size: 12px auto;}

.manage-list-wrap .tit-m{margin: 60px 0 70px;}
.manage-list-wrap .btn-wrap{margin-bottom: 26px;}
.manage-list-wrap .btn-wrap .btn-con{display: flex; justify-content: space-between;}
.manage-list-wrap .btn-wrap .btn-con .btn:nth-of-type(1){width: 182px;}
.manage-list-wrap .btn-wrap .btn-con .btn:nth-of-type(2){width: 152px;}

.manage-list-wrap .table-wrap.table1 .item p:nth-of-type(1){width: 11.5%;}
.manage-list-wrap .table-wrap.table1 .item p:nth-of-type(2){width: 23.5%;}
.manage-list-wrap .table-wrap.table1 .item p:nth-of-type(3){width: 17.6%;}
.manage-list-wrap .table-wrap.table1 .item p:nth-of-type(4){width: 9.4%;}
.manage-list-wrap .table-wrap.table1 .item p:nth-of-type(5){width: 14.4%;}
.manage-list-wrap .table-wrap.table1 .item p:nth-of-type(6){width: 23.6%;}

.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(1){width: 4.4%;}
.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(2){width: 12.3%;}
.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(3){width: 10.6%;}
.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(4){width: 18.7%;}
.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(5){width: 12.5%;}
.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(6){width: 4.8%;}
.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(7){width: 4.8%;}
.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(8){width: 10.8%;}
.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(9){width: 15.7%;}
.manage-list-wrap .table-wrap.table2 .item p:nth-of-type(10){width: 5.4%;}

.manage-list-wrap .table-wrap.table3 .item p:nth-of-type(1){width: 3.7%;}
.manage-list-wrap .table-wrap.table3 .item p:nth-of-type(2){width: 7.7%;}
.manage-list-wrap .table-wrap.table3 .item p:nth-of-type(3){width: 23.4%;}
.manage-list-wrap .table-wrap.table3 .item p:nth-of-type(4){width: 17.6%;}
.manage-list-wrap .table-wrap.table3 .item p:nth-of-type(5){width: 9.3%;}
.manage-list-wrap .table-wrap.table3 .item p:nth-of-type(6){width: 14.4%;}
.manage-list-wrap .table-wrap.table3 .item p:nth-of-type(7){width: 23.9%;}

.popup-wrap{display: none; overflow: auto hidden; z-index: 999; position: fixed; left: 0; top: 0; width: 100%; height: 100%; background-color: rgba(0,0,0,0.4); backdrop-filter: blur(3px);}
.popup-wrap .popup-frame{display: flex; align-items: center; justify-content: center; width: 100%; height: 100%;}
.popup-wrap .inner{position: relative; overflow: hidden auto; padding: 50px 70px; max-width: 1200px; max-height: 90%; width: 95%; background-color: rgba(255, 255, 255, 0.9); border-radius: 37px; -ms-overflow-style: none; scrollbar-width: none;}
.popup-wrap .inner::-webkit-scrollbar {display: none;}
.popup-wrap .inner .title{padding-bottom: 25px; border-bottom: 1px solid #D8D8D8;}
.popup-wrap .inner .title p{font-size: 28px; font-weight: 700;}
.popup-wrap .inner .detail-con{position: relative; padding-top: 36px;}
.popup-wrap .inner .detail-con .item-con{margin-bottom: 30px; font-size: 0;}
.popup-wrap .inner .detail-con .item-con:last-of-type{margin-bottom: 0;}
.popup-wrap .inner .detail-con .item{display: inline-block; width: 100%; vertical-align: top;}
.popup-wrap .inner .detail-con .item.half{width: 50%;}
.popup-wrap .inner .detail-con .item .flex{display: flex; align-items: center; margin-bottom: 20px; width: 100%;}
.popup-wrap .inner .detail-con .item .flex dt{width: 120px; font-size: 23px; font-weight: 700;}
.popup-wrap .inner .detail-con .item .flex dd{flex-wrap: nowrap; width: calc(100% - 120px);}
.popup-wrap .inner .detail-con .item .flex dd .date{max-width: 250px; height: 47px;}
.popup-wrap .inner .detail-con .item .flex dd .user{max-width: 185px; height: 47px;}
.popup-wrap .inner .detail-con .item .flex dd .course{max-width: 250px; height: 47px;}
.popup-wrap .inner .detail-con .item .flex dd .hole{margin-left: 20px; max-width: 95px; height: 47px;}
.popup-wrap .inner .detail-con .btn-con{justify-content: center;}
.popup-wrap .inner .detail-con .btn-con .btn{width: 182px;}
.popup-wrap .inner .detail-con .btn-con .btn.pos-r{position: absolute; right: 0;}
.popup-wrap.fixed .popup-frame{display: block; overflow: hidden auto; padding: 50px 0;}
.popup-wrap.fixed .popup-frame .inner{margin: 0 auto;}

.popup-wrap .inner .detail-con .item.flex{@include flex(); padding: 20px 0 50px;}
.popup-wrap .inner .detail-con .item .search-con{@include flex('space-between'); width: 100%; padding-right: 30px;}
.popup-wrap .inner .detail-con .item .search-con .inp{width: 59%;}
.popup-wrap .inner .detail-con .item .search-con .txt{margin-left: 20px; font-size: 20px; font-weight: 700;color: $c-blue;}
.popup-wrap .inner .detail-con .item .search-con .txt span{text-decoration: underline; vertical-align: middle;}
.popup-wrap .inner .detail-con .item .search-con .txt .ico-del{width: 20px; height: 20px; @include bg('common/ico_del.svg'); vertical-align: middle;}
.popup-wrap .inner .detail-con .item .inp-select.hole{margin-left: 13px; width: 134px;}

.popup-wrap .btn-close{position: absolute; top: 40px; right: 40px; width: 37px; height: 37px; @include bg('/common/btn_close.svg');}
.popup-wrap .tag{margin-bottom: 35px; width: 142px; height: 40px; font-size: 18px; background: $c-blue; border-radius: 40px;}
.popup-wrap .course-con{margin: 0 auto; width: 488px;}
.popup-wrap .course-con.full{@include flex(); flex-wrap: wrap; justify-content: space-between; padding: 0 30px; width: 100%;}
.popup-wrap .course-con.full .course-item{width: 48%;}
.popup-wrap .course-con .course-item .course-img{width: 100%; height: 402px; border-radius: 0;}
.popup-wrap .course-con .course-item .course-img + .course-img{margin-left: 45px;}
.popup-wrap .course-con .course-item .course-img .img-con{width: 300px; height: 300px;}
.popup-wrap .course-con .course-item .size{@include flex('center'); margin: 0 auto 7px; width: 32px; height: 32px; font-size: 18px; font-weight: 700; border: 1px solid #707070; background: #fff; border-radius: 50%;}
.popup-wrap .course-con .course-item.type2 .course-info{@include flex(); margin-bottom: 14px;}
.popup-wrap .course-con .course-item.type2 .course-info .size{margin: 0 10px 0 0;}
.popup-wrap .course-con .course-item.type2 .course-info span{font-size: 16px;}
.popup-wrap .course-con .info-box{margin-top: 50px; padding: 16px 20px; width: 100%; font-size: 18px; color: rgba(0, 0, 0, 0.6); background: #ECECEC; border: 1px solid #D8D8D8; border-radius: 3px;}
.popup-wrap .course-con .info-box p + p{margin-top: 6px;}
.popup-wrap .course-con .info-box strong{font-weight: 700;}

.popup-wrap .search-form{margin-bottom: 40px;}
.popup-wrap .search-form .date-con{margin-left: auto; margin-right: auto; }
.popup-wrap .search-form .data-list{margin-top: 20px; margin-left: auto; margin-right: auto; width: 70%;}

.popup-frame.alert .inner{padding: 15px 20px; width: 450px; border-radius: 6px;}
.popup-frame.alert .txt{padding: 40px 0; font-size: 22px; font-weight: 700; text-align: center;}
.popup-frame.alert .txt .count{display: inline-block; margin-top: 10px; padding: 2px 8px; font-size: 20px; font-weight: 700; color: #fff; text-decoration: underline; text-underline-position : from-font; background-color: #F0142F; border-radius: 14px;}
.popup-frame.alert .btn-today{display: flex; align-items: center; margin-top: 30px;}
.popup-frame.alert .btn-today input[type=checkbox]{margin-right: 4px; width: 16px; height: 16px;}

#register-pop.popup-wrap .inner .title{text-align: center; border-bottom: none;}
#register-pop.popup-wrap .inner .detail-con .item .inp-group{height: 47px;}
#register-pop.popup-wrap .inner .detail-con .item .inp-group.date{max-width: 229px;}
#register-pop.popup-wrap .inner .detail-con .item .inp-group ~ .inp-select{margin-left: 20px;}
#register-pop.popup-wrap .inner .detail-con .item .inp .inp-group.text-area{height: 130px;}


//write-wrap
.write-wrap .inp-con{@include flex();}
.write-wrap .bottom-con{@include flex(); margin-top: 40px;}
.write-wrap .bottom-con .btn-con{margin-left: 60px;}
.write-wrap .inp .inp-group.text-area{height: 480px;}

//setting-wrap
.setting-wrap .inp-con{width: 592px;}
.setting-wrap .btn-con{@include flex('center', ''); margin-top: 30px; margin-left: 150px; width: 442px;}

//notice-view-wrap
.notice-view-wrap .info-con{@include flex(); padding: 20px 40px; border: 1px solid #D8D8D8; border-radius: 3px;}
.notice-view-wrap .info-con p{font-size: 20px; flex-shrink: 0; text-align: center;}
.notice-view-wrap .info-con .category{@include flex('center'); margin-right: 40px; width: 150px; height: 35px; color: #fff; font-size: 16px; font-weight: 700; background: #AAAAAA; border-radius: 26px;}
.notice-view-wrap .info-con .txt{width: 100%; font-weight: 700; flex-shrink: 1; text-align: left;}
.notice-view-wrap .info-con .date{width: 15%; text-align: center;}
.notice-view-wrap .info-con .logo{width: 178px;}
.notice-view-wrap .txt-con{margin-top: 20px; padding: 150px; border: 1px solid #D8D8D8; border-radius: 3px;}
.notice-view-wrap .txt-con p{font-size: 20px; line-height: 1.4;}
.notice-view-wrap .txt-con img{margin-top: 65px;}

//answer-wrap
.answer-wrap{margin-top: 124px; padding: 40px 0 60px; border: 3px solid $c-red;}
.answer-wrap .inner-con{margin: 0 auto; padding: 0 60px; max-width: 1060px;}
.answer-wrap .status{@include flex('space-between'); padding-bottom: 30px; border-bottom: 1px solid #D8D8D8;}
.answer-wrap .status .logo{width: 208px;}
.answer-wrap .status .tag{margin: 0;}
.answer-wrap .txt{margin-top: 30px; font-size: 24px; line-height: 1.4;}

//box-list
.box-list{display: flex; flex-wrap: wrap;}
.box-list .item{@include flex('space-between'); margin: 0 21px 22px 0; padding: 22px 24px; width: calc(33.33% - 14px); border: 1px solid #D8D8D8; border-radius: 6px;}
.box-list .item:nth-child(3n){margin-right: 0;}
.box-list .item .txt-con{width: 284px;}
.box-list .item .info-con{padding-bottom: 8px; font-size: 18px; border-bottom: 1px solid #D8D8D8;}
.box-list .item .info-con .logo-con{@include flex('center'); }
.box-list .item .info-con .logo-con img{flex-shrink: 0; margin-right: 12px; width: 52px; height: 52px; border-radius: 50%; border: 1px solid #707070;}
.box-list .item .info-con .logo-con .tit{font-size: 20px; font-weight: 700;}
.box-list .item .info-con strong{font-weight: 700; color: #000;}
.box-list .item .info-con .txt{@include flex(); margin-top: 20px;}
.box-list .item .info-con .txt em{font-size: 16px; font-weight: 700;}
